.mid {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

::-webkit-scrollbar {
  width: 0.25rem;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: var(--primary);
}

@media only screen and (max-width: 500px) {
  .mid {
    flex-direction: column;
  }
  .players-container {
    max-width: calc(100% - 2rem);
    min-width: calc(100% - 2rem);
  }
}

.container {
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 992px) {
  .container {
    flex-direction: column;
    gap: 2rem;
  }
}

.top {
  padding: 1rem 0rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

.top > a {
  font-family: monospace;
  color: var(--accent);
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  direction: rtl;
}

.top > a:hover {
  text-decoration: underline;
}

.top-right {
  display: flex;
  flex-direction: row;
  gap: 1.5rem;
}

.top-btns {
  display: flex;
  flex: row;
  gap: 1rem;
  min-height: 4rem;
  align-items: flex-end;
}

.top-btns > button {
  padding: 0.5rem 0.8rem;
  font-size: 1rem;
  font-weight: 600;
  background-color: var(--bg);
  color: var(--primary);
  outline: none;
  border: 0.2rem solid var(--secondary-accent);
  height: min-content;
}

.top-btns > button:hover {
  cursor: pointer;
  color: var(--accent);
  transition: background 0.5s ease;
}

.top-btns > button:disabled {
  opacity: 60%;
}

.top-btns > button:disabled:hover {
  color: var(--primary);
}
